const NavStyle ={
    
    Absolute:{
        display:"block",
        position:"absolute",
        zIndex:"1",
        width:"100px",
        transition: "width 0.4s,height 0.7s",
        height:"40px",
        
    },
    Absolute_onClick:{
        display:"block",
        position:"absolute",
        zIndex:"1",
        width:"200px",
        transition: "width 0.4s,height 0.2s",
        height:"200px",
            
    },

    MenuBtn:{
    height:"50px",
    width:"100%",
    transition: "width 0.4s,height 0.2s",
    },
    MenuBtn_Post:{
    height:"50px",
    width:"100%",
    transition: "width 0.4s,height 0.2s",
    },

    Normal:{
        display:"block",
        width:"200px",
        height:"200px",
        justifyContent: 'center',
        backgroundColor:"red",
        },

    Pre_Animated:{
        display: "block",
        width: "100%",
        height: "200px",
        transition: "margin 0.5s",
        margin: "-15px 0 0 -250px",
        backgroundColor: "pink"
    },
    Post_Animated:{
        display: "block",
        width: "100%",
        height: "200px",
        transition: "margin 0.5s",
        marginTop: "0",
        backgroundColor: "pink"
    }
}

export {NavStyle};
