import React from 'react';
import {CssStyle,ContainerDiv,BuyBtn,CardHeader} from './CardsCss'




  class Card1 extends React.Component{

    //transfare props https://zhenyong.github.io/react/docs/transferring-props.html
    render(){
        return(
            <div style={CssStyle}>
                <h1 style={CardHeader}>T-shirts</h1>
                <br></br>
                <div style={ContainerDiv}>
                    <img className="BtnImgSize" src="https://img.pngio.com/plain-tshirt-png-plain-white-t-shirt-gildan-back-free-png-images-white-t-shirt-png-920_679.png"></img>
                    <p>Cost: $1</p>
                    <button style={BuyBtn} value="T1" onClick={this.props.handler}>T-Shirt 1</button>
                </div>
                
                <div style={ContainerDiv}>
                <img className="BtnImgSize" src="https://img.pngio.com/plain-tshirt-png-plain-white-t-shirt-gildan-back-free-png-images-white-t-shirt-png-920_679.png"></img>
                    <p>Cost: $1</p>
                    <button style={BuyBtn} value="T2" onClick={this.props.handler}>T-Shirt 2</button>
                </div>
                
                <div style={ContainerDiv}>
                <img className="BtnImgSize" src="https://img.pngio.com/plain-tshirt-png-plain-white-t-shirt-gildan-back-free-png-images-white-t-shirt-png-920_679.png"></img>
                    <p>Cost: $1</p>
                    <button style={BuyBtn} value="T3" onClick={this.props.handler}>T-Shirt 3</button>
                </div>
            </div> 
            
        ) 
    }
}
