import React from 'react';
import {Card1,Card2,Card3} from './Cards'
import {NavBtn,CardFuncHead,CardFuncBody} from './CardsCss'

class CardFunctions extends React.Component{
    constructor(props){
    super(props);
        this.state ={
            state:false,
            hover:false
        }

        this.handler = this.handler.bind(this)
    }

    handler(a){
        console.log(a.target.value)
        
    }
    

    ToggleHoverBtn=(e)=>{
        this.setState({hover:!this.state.hover})

        const c =e.target.value
        const d = parseInt(c)
        this.setState({HoverId:d})

    }

    ToggleDiv=(e)=>{
        const {show} = this.state

        const c =e.target.value
        const d = parseInt(c)
        this.setState({show:!show})
        this.setState({CardNr:d})

    }



    render(){
        //Change css on hover https://reactjs.org/docs/handling-events.html
        return(  
        <div className="menu_nav">
        
            <div style={CardFuncHead}>
            <button style={NavBtn} onClick={this.ToggleDiv} onMouseEnter={this.ToggleHoverBtn} onMouseLeave={this.ToggleHoverBtn} value="0">T-shirt</button>
            <button style={NavBtn} onClick={this.ToggleDiv} onMouseEnter={this.ToggleHoverBtn} onMouseLeave={this.ToggleHoverBtn} value="1">Jumper</button>
            <button style={NavBtn} onClick={this.ToggleDiv} onMouseEnter={this.ToggleHoverBtn} onMouseLeave={this.ToggleHoverBtn} value="2">Trousers</button>
            <button style={NavBtn} onClick={this.ToggleDiv} onMouseEnter={this.ToggleHoverBtn} onMouseLeave={this.ToggleHoverBtn} value={null}>X</button>
            </div>

            <div style={CardFuncBody}>
            { this.state.CardNr === 0?<Card1 handler= {this.props.updateItem}/>:false}
            { this.state.CardNr === 1?<Card2 handler= {this.props.updateItem}/>:false}
            { this.state.CardNr === 2?<Card3 handler= {this.props.updateItem}/>:false}
            </div>
        </div>
        );
    }

}

export default CardFunctions