import React from 'react';
import {NavStyle} from './Nav_Style';

class NavMenu extends React.Component{
    constructor(props){
        super(props);
        this.state={
            //is Div Visible?
            visible:true,
            MenuOpen: "Open Menu"
        }

    }

    changeAtt=()=>{
        const {visible} = this.state
        this.setState({visible:!visible})

        this.state.MenuOpen==="Open Menu"?this.setState({MenuOpen:"Close Menu"}):this.setState({MenuOpen:"Open Menu"});
    }



    render(){
        var isVisible = NavStyle.Pre_Animated;
        var DivScaling = NavStyle.Absolute;
        var BtnScaling = NavStyle.MenuBtn;

        if(this.state.visible === false){
            DivScaling = NavStyle.Absolute_onClick;
            isVisible = NavStyle.Post_Animated;
            BtnScaling = NavStyle.MenuBtn;
            
        }else{
            DivScaling = NavStyle.Absolute;
            isVisible = NavStyle.Pre_Animated;
            BtnScaling = NavStyle.MenuBtn_Post;
           
        }



        return(
            <div style={DivScaling} > 
                
                <button style={BtnScaling} onClick={this.changeAtt}>{this.state.MenuOpen}</button>
                <div style={isVisible} id="Inner_Side_Nav" >
 
                </div>
            </div>
        )
    }
}

export default NavMenu;